import Vue from "vue"
import VueRouter from "vue-router"
import Login from "../views/login/index.vue"
import Layout from '../views/layout'
import Intro from '@/views/Intro/Intro'
import NotFound from "../views/404/index.vue"
import api from '@/http/api'
import store from '@/store'
import {
  getIFramePath,
  getIFrameUrl
} from '@/utils/iframe'

Vue.use(VueRouter)

const routes = new VueRouter({
  routes: [{
      path: '/',
      name: '',
      component: Layout,
      children: [{
        path: '',
        name: '',
        component: Intro,
        meta: {
          icon: 'fa fa-home fa-lg',
          index: 0
        }
      }]
    },
    {
      path: '/login',
      name: '',
      component: Login
    },
    {
      path: '/404',
      name: 'notFound',
      component: NotFound
    }
  ]
})
routes.beforeEach((to, from, next) => {
  // 
  // 
  let userName = sessionStorage.getItem('user')
  if (to.path === '/login') {
    // 
    if (userName) {
      next({
        path: '/'
      })
    } else {
      next()
    }
  } else {
    if (!userName) {
      // 
      next({
        path: '/login'
      })
    } else {
      // 
      addDynamicMenuAndRoutes(userName, to, from)
      next()
    }
  }
})

/**
 * 
 */
function addDynamicMenuAndRoutes(userName, to) {
  // IFrame
  handleIFrameUrl(to.path)
  if (store.state.app.menuRouteLoaded) {
    console.log('.')
    return
  }
  if (store.state.app.menuRouteLoaded) {
    console.log('.')
    return
  }
  api.menu.findNavTree({
      'userName': userName
    })
    .then(res => {
      // 
      let dynamicRoutes = addDynamicRoutes(res.data)
      routes.options.routes[0].children = routes.options.routes[0].children.concat(dynamicRoutes)
      routes.addRoutes(routes.options.routes)
      // 
      store.commit('menuRouteLoaded', true)
      // 
      store.commit('setNavTree', res.data)
    }).then(res => {
      console.log(res)
      api.user.findPermissions({
        'name': userName
      }).then(res => {
        // 
        store.commit('setPerms', res.data)
      })
    })
    .catch(function (res) {
      console.log(res)
    })
}
/**
 * IFrame
 */
function handleIFrameUrl(path) {
  // iframeUrlstoreIFrame
  let url = path
  let length = store.state.iframe.iframeUrls.length
  for (let i = 0; i < length; i++) {
    let iframe = store.state.iframe.iframeUrls[i]
    if (path != null && path.endsWith(iframe.path)) {
      url = iframe.url
      store.commit('setIFrameUrl', url)
      break
    }
  }
}
/**
 * ()
 * @param {*} menuList 
 * @param {*} routes ()
 */
function addDynamicRoutes(menuList = [], routes = []) {
  var temp = []
  for (var i = 0; i < menuList.length; i++) {
    if (menuList[i].children && menuList[i].children.length >= 1) {
      temp = temp.concat(menuList[i].children)
    } else if (menuList[i].url && /\S/.test(menuList[i].url)) {
      menuList[i].url = menuList[i].url.replace(/^\//, '')
      // 
      var route = {
        path: menuList[i].url,
        component: null,
        name: menuList[i].name,
        meta: {
          icon: menuList[i].icon,
          index: menuList[i].id
        }
      }
      let path = getIFramePath(menuList[i].url)
      if (path) {
        console.log("")
        console.log(path)
        // , iframe
        route['path'] = path
        route['component'] = resolve => require([`@/components/IFrame`], resolve)
        // URL
        let url = getIFrameUrl(menuList[i].url)
        let iFrameUrl = {
          'path': path,
          'url': url
        }
        store.commit('addIFrameUrl', iFrameUrl)
      } else {
        try {
          // URLvuevueurl
          // url="sys/user""@/views/sys/user.vue",
          let array = menuList[i].url.split('/')
          let url = ''
          for (let i = 0; i < array.length; i++) {
            url += array[i].substring(0, 1).toUpperCase() + array[i].substring(1) + '/'
          }
          url = url.substring(0, url.length - 1)
          route['component'] = resolve => require([`@/views/${url}`], resolve)
        } catch (e) {
          console.log(e)
        }
      }
      routes.push(route)
    }
  }
  if (temp.length >= 1) {
    addDynamicRoutes(temp, routes)
  } else {
    console.log('...')
    console.log(routes)
    console.log('.')
  }
  return routes
}
export default routes